body{
    
    overflow-x: hidden;
}


.bgShoe{
    background-image: url("https://lovelytab.com/wp-content/uploads/2019/01/Tumblr-Aesthetic-Wallpapers-Free.jpg") ;
    background-position: center;
    background-size: cover;
}
.slider{
     
    display: flex;
    width: 500vw;
    transition: all 1s ease-in-out;
} 

.sliderItem{
    width: 100vw;
    align-items: center;
    display: flex;
    justify-content: center;
    position: relative;
    text-align: center;
    padding: 0 200px;
}

.sliderTitle{
    color: aliceblue;
    position: absolute;
    top: 20%;
    right: 10%;
    font-weight: 900;
    font-size: 100px;   
}
.sliderPrice{
    
    color: aliceblue;
    position: absolute;
    right: 25%;
    top: 35%;
    font-size: 30px;
    font-weight: 400;
    
}

.sliderBuy{
    color: aliceblue;
    background-color: black;
    position: absolute;
    right: 13%;
    top: 35%;
    font-size: 25px;
    font-weight: 400;
    border-radius: 15px;
    border: none;
}
.sliderBuy:hover{
    color: black;
    background-color: aliceblue;
}

.features{
    background-color: rgb(255, 255, 255);
    display: flex;
    justify-content: space-around;
}

.feature{
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.featureheading{
    color: black;
    font-weight: 600;
}

.featuredes{
    color: black;
    font-weight: 300;
}